/****************************************** NAVBAR ********************************************/

nav {
  background: linear-gradient(90deg, #a9643e 50%, #161616 50%);
  z-index: 2;
}

.logo {
  width: 4.5rem;
  margin-left: 4rem;
  margin-top:  1rem;
}

.navbar-collapse {
  margin-right: 1rem;
  text-align: right;
}
.navbar-toggler {
  margin-right: 3rem;
}

/*----- MENU LINKS --------*/
nav a.nav-link {
  font-size: 20px;
  letter-spacing: 0.03rem;
  cursor: pointer;
}

nav ul li {
  text-transform: uppercase;
}

.navbar-nav > li > a {
  margin-right: 2rem;
  margin-left: 2rem;
  color: #ffffff !important;
  text-align: right;
  margin-top:  1.3rem;
}

.navbar-brand {
  max-width: 20rem;
  padding: 0 !important;
  height: 5rem !important;
}

.navbar-nav > li > a.active,
.navbar-nav > li > a.active:focus {
  border-top: 0.1875rem solid #a9643e;
}
.navbar-nav > li > a:hover {
  border-top: 0.1875rem solid #a9643e;
}

