    padding-bottom: 2rem;
    background: #161616;
    padding: 0.7rem;
    padding-top: 3rem;
    height: 50%;
  }
  .banner-image {
    width: 21rem;
  }
  .banner-image-box {
    position: relative;
    padding: 2px;
  }
  .overlay {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    height: 100%;
    width: 100%;
    opacity: 0;
    transition: .3s ease;
    background-color: #000000;
  }
  .banner-image-box:hover .overlay {
    opacity: 0.5;
  }
  .txtGraphicDesign{ 
    text-align: center;
    text-transform: uppercase;
    font-size: 4rem;
    color: #ffffff;
    padding-bottom: 20px;
  }
  .borderLogoBrand{
    border: 1px solid #ffffff;
    padding: 15px;
    width: 89.8%;
    text-align: center;
    display: inline-block;
  }
  .textBanner{
    color: #ffffff;
    font-size: 30px;
    text-transform: uppercase;
  }
}

