/****************************************** Contacts COMPONENT ********************************************/

.contacts {
  background: #a9643e;
  padding: 3rem 0;
  padding-top: 7.5rem;
  padding-bottom: 7rem;
  height: 100%; 
}

.contacts h1 {
  color: #ffffff;
}

.txtContacts {
  color: #ffffff;
  padding: 1.3rem 0;
}

.contacts p {
  color: #F5C5AA;
  font-size: 1rem;
}

.rowTextContacts {
  -webkit-justify-content: space-around;
          justify-content: space-around;
  color: #ffffff;
  margin-top: 4rem;
  padding-left: 15rem;
  padding-right: 15rem;
  border: 1px solid;
}

.picContacts {
  width: 60px;
}
.boXL1{
  margin-left: 120px;
}
.boXL2{
  text-align: left;
  margin-right: 100px;
}
.txtMe {
  font-size: 1.25rem;
  color: #ffffff;
}
.marginTop {
  margin-top: 5px;
  font-size: 1.3rem;
  color: #F5C5AA;
}

.areaInput {
  border: 1px solid;
}

.rowContacts {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
}

.txtInput {
  font-size: 2rem;
  color: #ffffff;
}
.areaForm {
  box-sizing: content-box;
  width: 600px;
  height: 300px;
}

.boxName {
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
}

.boxEmail {
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
  padding-top: 45px;
}

/****************************************** INPUT ********************************************/
/*------------------ INPUTS -------------------------------*/
.contacts input,
.contacts textarea {
  width: 100%;
  height: 50px;
  box-sizing: border-box;
  background: none;
  resize: none;
  border: 0;
  border: 0.125rem solid #ffffff;
  color: #F5C5AA;
  border-radius: 10px;
  outline: 0 !important;
  box-shadow: none !important;
  margin-bottom: 1rem;
  padding-left: 0 !important;
}

.contacts .container {
  padding: 4rem 0;
}

.contacts input:focus,
.contacts textarea:focus {
  border: 1px #ffffff;
  padding-left: 20px;
}

.contacts textarea {
  height: 13rem !important;
  padding-left: 20px;
}

.contact-btn {
  margin: 1rem 0 0 0;
  width: 100%;
  height: 50px;
  font-size: 20px;
  text-transform: uppercase;
  border-radius: 10px;
  background: #ffffff;
  border: 0px;
  color: #F5C5AA
}
.contact-btn:hover{
  background: #F5C5AA;
  color: #ffffff;
}
input:focus + .line,
textarea:focus + .line {
  width: 100%;
  transition: width 0.5s ease-in-out;
  padding-left: 20px;
}

input.form-control {
  margin: 0.3rem 0;
  padding-left: 20px;
}

textarea.form-control {
  margin: 0.3rem 0;
}

.error-message {
  color: #ff0000;
  text-align: left;
}

.success-message {
  font-weight: 900;
  color: #00ff7f;
}

